import route from "@/common/public.js"
import iPrice from '@/components/i-price/i-price.vue'
import Vue from 'vue'
import lazy from "@/common/vue-lazyload.js"
import copyright from "@/components/customize/copyright.vue"
import tarBar from "@/components/customize/tarBar.vue"
Vue.use(lazy)
export default {
	components: {
		iPrice,
		copyright,
		tarBar
	},
	data() {
		return {
			imgUrl :'',  //图片访问ip
			isHeaderColor:true,//header在首页顶部时背景色透明
			background: ['color1', 'color2', 'color3'],
			indicatorDots: false, //是否显示面板指示点
			autoplay: true,  //是否自动切换
			circular:true,  //是否衔接切换
			interval: 3000,  //自动切换时间间隔
			duration: 500  ,//滑动动画时长
			ListOfGoods:'', //商品列表
			wheelPlanting: '', //轮播图片
			Signboard: '',
			Signguid: '',
			//滚动条位置
			scrollTop:0, 
			old: {  
				scrollTop: 0 
			},
			userName: '',  //登录用户名
			userActive: false,  //登录按钮
			userImgActive: true,  //娃娃头像
			shop: '0',  //购物车数量
			dis: 0,//判断获取的数据是否是已经登陆后的数据，0是未登录数据，1是已登陆数据
		}
	},
	methods: {
		// 获取轮播和产品数据
		getData:function(){
			var jsonList=uni.getStorageSync("jsonList");
			if(jsonList){
				var data = JSON.parse(jsonList); //JSON字符串转对象
				this.Signboard = data.Ident_Signboard;
				this.Signguid = data.Ident_Signguid;
			}
			uni.request({
				url:route.variable+'/mobile/index/index',
				method: 'GET',
				data: {
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid,
				},
				success: res => {
					if(res.statusCode == 200){
						// 商品列表
						this.ListOfGoods = res.data.goods;
						// 轮播图片
						this.wheelPlanting = res.data.lun;
						if(res.data.dis == 1){
							this.dis == 1
						}
					}
				},
			});	
		},
		//记录滚动条位置
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
			// header在滚动高度大于该值时（默认设置的20）,就会去掉透明度
			if(this.old.scrollTop>20){
				this.isHeaderColor=false;
			}else{
				this.isHeaderColor=true;
			}
		},
		//记录滚动条位置
		backTop(evt) {
			this.scrollTop = this.old.scrollTop
			this.$nextTick(function() {
				this.scrollTop = 0
			});
		},
		search(){
			uni.navigateTo({
				url: "/pages/index/search",
			})
		},
		// 轮播开始
		changeIndicatorDots(e) {
			this.indicatorDots = !this.indicatorDots
		},
		changeAutoplay(e) {
			this.autoplay = !this.autoplay
		},
		intervalChange(e) {
			this.interval = e.target.value
		},
		durationChange(e) {
			this.duration = e.target.value
		},
		// 轮播结束
		judge(e){
			uni.showToast({
				title: '请登录后再进行下面操作',
				icon: 'none'
			})
			uni.navigateTo({
				url: 'login'
			})
			return false
		},
		// 跳转链接 
		btnJump(evt) { //option为object类型，会序列化上个页面传递的参数
			if(this.userName == ''){
				this.judge();
			}else{
				/* 根据判断evt参数来跳转页面 */
				switch (evt){
					case 1:
						uni.navigateTo({
							url: '../product/product'
						});
						break;
					case 2:
						uni.navigateTo({
							url: '../user/recharge_center'
						});
						break;
					case 3:
						uni.navigateTo({
							url: '../order/orderpageinfo'
						});
						break;
					case 4:
						uni.navigateTo({
							url: '../user/safety'
						});
						break;
					case 5:
						uni.navigateTo({
							url: '../user/personalInfo'
						});
						break;
					case 6:
						uni.navigateTo({
							url: '../shopping/shopping'
						});
						break;
					case 7:
						uni.navigateTo({
							url: '../user/useraddress'
						});
						break;
					case 8:
						uni.navigateTo({
							url: '../user/bill'
						});
						break;
					default:
						uni.navigateTo({
							url: '../index/index'
						});
						break;
				}
			}
		},
		// 跳转分类
		classification(e){
			if(this.userName == ''){
				this.judge();
			}else{
				uni.navigateTo({
					url: '../product/product'
				})
			}
		},
		// 搜索框
		leftBars(e){
			uni.navigateTo({
				url: "../product/product"
			})
		},
		// 点击娃娃头像:跳转到用户中心
		userImgTap(e){
			if(this.userName == ''){
				this.judge();
			}
			uni.navigateTo({
				url: '../user/user'
			})
		},
		/* 点击退出触发的事件 */
		OutClick(evt){
			uni.request({
				url:route.variable+'/mobile/index/loginOut',
				method:'GET',
				data:{
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid,
				},
				success: (res) => {
					if(res.data.status == 0){
						uni.clearStorageSync();
						uni.showToast({title: res.data.message,icon:'none'});
						this.userImgActive = true;
						this.userActive = false;
					}
				}
			})
		}
	},
	// 数据接口请求( 初始化页面完成时触发)
	onLoad() {
		this.imgUrl = route.variable;  //图片访问IP
		
		this.getData();
	},
	onShow() {
		let result = route.checkStatus();
		this.userName = result.userName;
		this.userImgActive = result.userImgActive;
		this.userActive = result.userActive;
		
		var isLogin = uni.getStorageSync('isLogin');
		if(isLogin){
			if(isLogin == 1&&this.dis==0){
				this.getData();
			}
		}else{
			this.dis = 0
		}
		
		var jsonList=uni.getStorageSync("jsonList");
		if(jsonList){
			var data = JSON.parse(jsonList); //JSON字符串转对象
			this.shop = data.shop;  //购物车数量
		}else{
			this.shop = '0';
		}
		if(this.shop == '0'){  // 购物车数据为0 就移除购物车角标
			uni.removeTabBarBadge({
				index:2
			})
		}else{
			uni.setTabBarBadge({
				index: 2,
				text: this.shop+''
			})
		}
	},
	onReady() {
	}
}
